@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap);
.landingHeader {
  margin-top: 220px;
}

.welcome {
  font-weight: bold;
  font-size: 30px;
  color: white;
}

.landingFlex {
  display: flex;
  align-items: center;
  margin: auto;
  max-width: 10%;
  justify-content: center;
}

.myLink {
  text-decoration: none;
  background-color: white;
  padding: 8px 15px;
  border-radius: 5px;
  margin-right: 15px;
  color: black;
}

.myLink:hover {
  text-decoration: none;
  color: black;
}

@media (max-width: 768px) {
  .landingFlex {
    display: flex;
    align-items: center;
    margin: auto;
    max-width: 50%;
    justify-content: center;
  }
}

.main-content {
  margin: auto;
  max-width: 60%;
}

.add-task {
  padding: 11px;
  margin-right: 5px;
  border: 1px solid #ccc;
}

.add-task:focus {
  outline: 0;
}

.btn-add {
  padding: 10px;
  margin-top: -3px;
}

.userData {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.name-data {
  font-weight: bold;
  font-size: 18px;
}

.task-list {
  margin: 40px auto;
  padding: 20px;
  border: 1px solid #ccc;
  background-color: #f4f4f4;
}

.filter {
  padding: 6px;
  border: 1px solid #ccc;
  margin-right: 50px;
}

.filter:focus {
  outline: 0;
  border: 1px solid #ccc;
}

.no {
  font-weight: bold;
}

.task-item {
  margin: 10px auto;
  display: grid;
  grid-template-columns: 0.5fr 2fr 0.5fr;
  grid-gap: 7px;
  border: 1px solid #ccc;
  width: 100%;
  padding: 5px;
}

@media (max-width: 768px) {
  .main-content {
    margin: auto;
    max-width: 90%;
  }
}

@media (max-width: 425px) {
  .main-content {
    margin: auto;
    max-width: 100%;
  }

  .addTaskFlex {
    display: flex;
    flex-direction: column;
  }

  .add-task {
    min-width: 100%;
    border-radius: 5px;
  }

  .btn-add {
    margin: 5px auto;
    max-width: 50%;
  }

  .userData {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .filterFlex {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .filter {
    width: 100%;
    margin-bottom: 10px;
  }

  .task-item {
    margin: 10px auto;
    display: grid;
    grid-template-columns: 2fr;
    grid-gap: 7px;
    border: 1px solid #ccc;
    width: 100%;
    padding: 5px;
  }
}

body {
  font-family: "Josefin Sans", sans-serif;
  background-image: url(/TO-DO-LIST%20React-Redux/static/media/mybg.b74f9955.jpg);
  background-color: #ffffff;
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  overflow-y: hidden;
}

.page-header {
  font-weight: bold;
}

.form-container {
  margin: auto;
  max-width: 50%;
  background: #ccc;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04), 0px 2px 6px rgba(0, 0, 0, 0.04),
    0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 10px;
  padding: 30px;
}

.sign-up {
  font-weight: bold;
}

.errormessage {
  background-color: #ffc0cb8a;
  text-align: center;
  padding: 5px;
}

label {
  font-weight: bold;
}

.form-text {
  font-size: 12px;
  font-weight: bold;
}

.errorMessage {
  font-family: sans-serif;
  color: red;
  font-size: 8px;
  padding-top: 5px;
}

i {
  position: absolute;
  top: 63%;
  right: 30%;
}

i:hover {
  color: #00fcb6;
  cursor: pointer;
}

@media (max-width: 425px) {
  .form-container {
    margin: auto;
    max-width: 95%;
    border: 1px solid #ccc;
    padding: 30px;
  }
}

